import React, { useContext, useEffect, useState } from "react";
import { simpleCredentialGet } from "./api/APIconnexion";
import "./app.css";
import Chat from "./components/Chat";
import ErrorModal from "./components/ErrorModal";
import LoginSub from "./components/LoginSub";
import { AuthContext } from "./context/AuthContext";
import { ErrorContext } from "./context/ErrorContext";
import { ADDRESS } from "./helpers/Address";

function App() {
  const [err, setErr] = useContext(ErrorContext);
  const [token, setToken] = useContext(AuthContext);
  const [id, setId] = useState<string>("");

  useEffect(() => {
    if (token) return;

    let auth = localStorage.getItem("token");
    
    if (auth) {
      simpleCredentialGet(ADDRESS.cookieValidate, auth).then((data:any)=>{
        setId(data.id);
        setToken(auth);
      });
      
    }
  }, [token]);

  return (
    <div className="App">
      {err ? <ErrorModal /> : ""}
      {token ? (
        <Chat id={id} />
      ) : (
        <LoginSub setId={setId} err={err} setErr={setErr} />
      )}
    </div>
  );
}

export default App;
